form {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1.5rem;
  padding: 2rem;
  border-radius: 15px;
  background: linear-gradient(to right, lightblue, rgb(245, 214, 214)); 
  box-shadow: 0px 8px 20px rgba(0, 0, 0, 0.2);
}

label,
select,
input,
button {
  font-family: 'Roboto', sans-serif;
  font-size: 16px;
  padding: 0.8rem;
  border: none;
  border-radius: 10px;
  box-sizing: border-box;
  transition: all 0.3s ease-in-out;
}

label {
  width: 100px;
  text-align: right;
  margin-right: 10px;
}

select,
input[type="date"] {
  background-color: white ;
  border: 1px solid black;
  width: 150px;
}

button {
  background-color: blue;
  border: 1px solid black;
  color: white;
  cursor: pointer;
}

button:hover {
  background-color: darkblue;
  transform: scale(1.05);
}

label:hover,
select:hover,
input:hover {
  background-color: white;
}

.ticket {
  width: 70%;
  margin: 20px auto;
  background-color: white;
  padding: 40px;
  border: 1px solid black;
  border-radius: 15px;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.15);
  font-family: 'Open Sans', sans-serif;
}

.ticket h3 {
  color: blue;
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 30px;
  text-align: center;
}

.ticket-details {
  display: grid;
  align-items: start;
}

.ticket-info {
  margin-bottom: 15px;
  justify-content: space-around;
}

.label {
  font-weight: bold;
  margin-right: 10px;
}

.book-button {
  background-color: blue;
  color: white;
  padding: 15px 30px;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  font-size: 18px;
  text-align: center; 
  display: block;
  margin: 0 auto;
}

.book-button:hover {
  background-color: darkblue;
  transform: scale(1.02);
}

.ticket:hover {
  box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.2);
}

p,h2{
  text-align: center;
}

span{
  margin: 5%;
}

.error{
  color: red;
}
